<template>
  <div class="company-introduce-container">
    <div class="main-card">
      <div class="main-header">
        <span class="main-title">政策规划</span>
        <div class="close-btn" @click="cancelBtn">×</div>
      </div>

      <div class="contentBox">
        <div class="tab">
          <a class="tab-btn" href="javascript:;" :class="activeTab == '国家' ? 'active' : ''" @click="tabClick('国家')">国家</a>
          <a class="tab-btn" href="javascript:;" :class="activeTab == '市级' ? 'active' : ''" @click="tabClick('市级')">市级</a>
          <a class="tab-btn" href="javascript:;" :class="activeTab == '区级' ? 'active' : ''" @click="tabClick('区级')">区级</a>
          <input type="text" class="searchStl" v-model="searchIpt" placeholder="请输入政策关键字搜索">
          <i class="el-icon-search iconStl" @click="requestTableData"></i>
        </div>
        <div class="table_box">
          <div class="table_header">
            <span style="width: 150px">序号</span>
            <span style="width: 442px">文件名称</span>
            <span style="width: 442px">文号</span>
            <span style="width: 442px">发文单位</span>
            <span style="width: 442px">发布日期</span>
          </div>

          <div class="table_body">
            <vue-seamless-scroll :data="tableData" class="seamless-warp" :class-option="classOption">
              <div v-for="(item,index) in tableData" :key="index" class="my-table-content">
                <span style="width: 150px">{{index + 1}}</span>
                <span style="width: 442px">
                  <a :href="item.weburl" target="_blank">{{item.policyName}}</a>
                </span>
                <span style="width: 442px">{{item.documentNumber}}</span>
                <span style="width: 442px">{{item.publishDepartment}}</span>
                <span style="width: 442px">{{item.publishDate}}</span>
              </div>
            </vue-seamless-scroll>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { http } from "@/utils/request";
import BottomLine from "@/components/BottomLine";

import Breadcrumb from "@/components/Breadcrumb";
import CloseBtn from "@/components/CloseBtn";
export default {
  data() {
    return {
      tableData: [],
      searchIpt: "",
      activeTab: "国家",
    };
  },
  mounted() {
    this.requestTableData();
  },
  computed: {
    classOption() {
      return {
        step: 0.5, // 数值越大速度滚动越快
        // limitMoveNum: 10, // 开始无缝滚动的数据量 this.dataList.length
        hoverStop: true, // 是否开启鼠标悬停stop
        direction: 1, // 0向下 1向上 2向左 3向右
        // openWatch: true, // 开启数据实时监控刷新dom
        singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
        singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
        waitTime: 1000, // 单步运动停止的时间(默认值1000ms)
      };
    },
  },
  methods: {
    cancelBtn() {
      this.$router.push({ name: "Home" });
    },
    tabClick(activeTab) {
      this.activeTab = activeTab;
      this.tableData = [];
      this.requestTableData();
    },
    requestTableData() {
      let params = {
        level: this.activeTab,
        policyName: this.searchIpt,
      };
      http.get("/policy_plan/policyPlanQueryPage", { params }).then((res) => {
        this.tableData = res.data.result;
        this.tableData.forEach((item) => {
          item.publishDate = item.publishDate.slice(0, 10);
        });
      });
    },
  },
  components: {
    Breadcrumb,
    CloseBtn,
    BottomLine,
  },
};
</script>

<style lang="scss" scoped>
.company-introduce-container {
  width: 2054px;
  height: 969px;
  .main-header {
    position: relative;
  }
  .contentBox {
    position: relative;
    margin-top: 80px;
    width: 1919px;
    height: 730px;
    .tab {
      position: relative;
      .searchStl {
        width: 500px;
        height: 50px;
        padding-left: 20px;
        position: absolute;
        top: 0px;
        right: 200px;
        background-color: #041b3b;
        border-radius: 30px;
        border: 1px solid #0264ac;
        color: #fff;
      }
      .iconStl {
        position: absolute;
        top: 20px;
        right: 230px;
        font-size: 20px;
        cursor: pointer;
      }
      .tab-btn {
        font-size: 26px;
        line-height: 29px;
        display: inline-block;
        padding: 0 30px 10px;
        border-bottom: 2px solid #439799;
        &.active {
          // color: #439799;
          border-bottom: 4px solid #60cfff;
          color: #60cfff;
        }
      }
    }
    .table_box {
      width: 1919px;
      height: 750px;
      margin-top: 30px;

      .table_header {
        span {
          height: 70px;
          display: inline-block;
          line-height: 70px;
          font-size: 26px;
          text-align: center;
          color: #fff;
        }
        // border-bottom: 1px solid #439799;
      }
      .table_body {
        width: 1919px;
        height: 620px;
        overflow: hidden;
        .my-table-content:nth-child(2n + 1) {
          background: none;
        }
        .my-table-content:nth-child(2n) {
          background: linear-gradient(
            to right,
            rgba(74, 129, 169, 0) 0%,
            #447ca5 50%,
            rgba(74, 129, 169, 0) 100%
          );
        }
        div {
          // border-bottom: 1px solid #439799;
          span {
            height: 70px;
            display: inline-block;
            line-height: 70px;
            font-size: 26px;
            text-align: center;
            color: #fff;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
        }
      }
    }
  }
}
input::-webkit-input-placeholder {
  /* placeholder颜色  */
  color: #fff;
  /* placeholder字号  */
  font-size: 0.14rem;
}
input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #fff;
  font-size: 0.14rem;
}
input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #fff;
  font-size: 0.14rem;
}
input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #fff;
  font-size: 0.14rem;
}
</style>